@import url(https://fonts.googleapis.com/css2?family=KoHo:wght@300&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "KoHo", sans-serif;
}

.container {
  background-color: #060822;
  min-height: 100vh;
  width: 100%;
  padding: 50px 0px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.logo {
  height: 100px;
  cursor: pointer;
}
.figure {
  text-align: center;
  margin: 20px 0px;
}
figcaption {
  font-size: 1.3rem;
  font-weight: 600;
  color: #fff;
  margin: 10px 0px;
  word-spacing: 5px;
}
@media (max-width: 1024px) {
  .container {
    padding-bottom: 860px;
  }
}
@media (max-width: 1000px) {
  .container {
    padding-bottom: 200px !important;
  }
}
@media (max-width: 849px) {
  .container {
    padding-bottom: 260px !important;
  }
}
@media (max-width: 799px) {
  .container {
    padding-bottom: 200px !important;
  }
}
@media (max-width: 530px) {
  .container {
    padding-bottom: 100px !important;
  }
}

.inputDiv {
  background-color: #fff;
  padding: 5px 15px;
}
input {
  font-size: 1.1rem;
  padding: 5px 10px;
  font-weight: 600;
  margin-right: 30px;
  background: transparent;
  border: none;
}

input:focus {
  outline: none;
}
.btn {
  font-size: 1.1rem !important;
  color: blueviolet;
  cursor: pointer;
  transition: font-size 0.6s ease;
}
.btn:hover {
  font-size: 1.3rem !important;
}

.modalDiv {
  height: auto;
  width: 30%;
  background-color: #ccc;
  border-radius: 10px;
  padding: 10px 30px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  z-index: 100;
  cursor: pointer;
}
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}
h3 {
  margin-bottom: 20px;
  color: blueviolet;
}
.backDrop {
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 10;
  cursor: pointer;
}

@media (max-width: 1024px) {
  .modalDiv {
    top: 20% !important;
    width: 35% !important;
  }
}
@media (max-width: 900px) {
  .modalDiv {
    top: 30% !important;
    width: 50% !important;
  }
}
@media (max-width: 605px) {
  .modalDiv {
    top: 40% !important;
    width: 70% !important;
  }
}
@media (max-width: 501px) {
  .modalDiv {
    top: 50% !important;
    width: 90% !important;
  }
}

.listDiv {
  height: auto;
  width: 100%;
  margin: 10px 0px;
  /* border: 1px solid #fff; */
  text-align: center;
}
.editDiv {
  background-color: #fff;
  padding: 5px 15px;
  margin: 15px 0px;
}
input {
  font-size: 1.1rem;
  padding: 5px 10px;
  font-weight: 600;
  margin-right: 30px;
  background: transparent;
  border: none;
}

input:focus {
  outline: none;
}
.btn {
  font-size: 1.1rem !important;
  color: blueviolet;
  cursor: pointer;
  transition: font-size 0.6s ease;
}
.btn:hover {
  font-size: 1.3rem !important;
}
.list {
  margin: 20px auto;
  /* border: 1px solid #fff; */
  height: auto;
  width: 30%;
  padding: 5px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: blueviolet;
  border-radius: 5px;
  transition: all 0.6s ease;
}
.list:hover {
  background-color: #fff;
}
.list:hover .listData {
  color: blueviolet;
}
.list:hover .editBtn {
  color: #060822;
}
.list:hover .deleteBtn {
  color: rgb(228, 82, 82);
}
.listData {
  font-size: 1.2rem;
  color: #fff;
  font-weight: 600;
}
.editBtn {
  font-size: 1.1rem;
  color: #fff;
  font-weight: 600;
  cursor: pointer;
  margin-right: 20px;
}

.deleteBtn {
  font-size: 1.1rem;
  font-weight: 600;
  color: #fff;
  cursor: pointer;
  transition: color 0.6s ease;
}
.clearBtn {
  font-size: 1.1rem;
  padding: 5px 20px;
  background-color: blueviolet;
  color: #fff;
  border: none;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.7s ease;
}
.clearBtn:hover {
  background-color: #fff;
  color: #060822;
}
.clearBtn:focus {
  outline: none;
}

@media (max-width: 950px) {
  .list {
    width: 40% !important;
  }
}
@media (max-width: 650px) {
  .list {
    width: 70% !important;
  }
}
@media (max-width: 501px) {
  .list {
    width: 80% !important;
  }
}

